import { inject, injectable } from 'inversify'
import { TrpcService } from './trpc.service'
import type { LoggerService } from '@nestjs/common'
import { z } from 'zod'
import { FlowdaTrpcClientSymbol, PrismaClientSymbol } from '@flowda-projects/flowda-shared-types'
import * as db from '@prisma/client-cms_admin'
import type { CreateTRPCProxyClient } from '@trpc/client'
import type { AppRouter } from '@flowda-projects/flowda-services-trpc-server'

export const queryLinksSchema = z.object({
  projectId: z.string(),
})
export const queryProjectUsersSchema = z.object({
  projectId: z.string(),
})
export const updateProjectSchema = z.object({
  previousSlug: z.string(),
  slug: z.string().optional(),
  name: z.string().optional(),
})

@injectable()
export class ProjectRouter {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject(FlowdaTrpcClientSymbol) private flowdaTrpc: CreateTRPCProxyClient<AppRouter>,
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(ProjectRouter.name)
  }

  router = this.trpc.router({
    queryLinks: this.trpc.procedure.input(queryLinksSchema).query(async ({ input }) => {
      const siteRet = await this.prisma.site.findUniqueOrThrow({
        where: {
          projectId: input.projectId,
        },
      })
      return siteRet
    }),

    queryProjectUsers: this.trpc.procedure.input(queryProjectUsersSchema).query(async ({ input }) => {
      const projectUsersRet = await this.prisma.projectUsers.findMany({
        where: {
          projectId: input.projectId,
        },
        select: {
          userId: true,
        },
      })
      const userIds = projectUsersRet.filter(i => i.userId != null).map(i => Number(i.userId))
      const usersRet = await this.flowdaTrpc.user.findMany.query({ userIds: userIds })
      return projectUsersRet.map(ret => {
        return Object.assign(ret, {
          user: usersRet.find(u => u.id === Number(ret.userId)),
        })
      })
    }),

    updateProject: this.trpc.procedure.input(updateProjectSchema).mutation(async ({ input }) => {
      this.logger.debug!(`[updateProject] invoked, ${JSON.stringify(input, null, 2)}`)
      const ret = await this.prisma.project.update({
        where: {
          slug: input.previousSlug,
        },
        data: {
          ...(input.name && { name: input.name }),
          ...(input.slug && { slug: input.slug }),
        },
      })
      return ret
    }),

    findProjectOwner: this.trpc.procedure
      .input(
        z.object({
          slug: z.string(),
        }),
      )
      .query(async ({ input }) => {
        const projectUserRet = await this.prisma.projectUsers.findFirst({
          where: {
            role: 'owner',
            project: {
              slug: input.slug,
            },
          },
        })
        if (projectUserRet?.userId != null) {
          const userRet = await this.flowdaTrpc.user.findUnique.query({ id: Number(projectUserRet.userId) })
          if (userRet) {
            return {
              email: userRet.email,
            }
          } else {
            return null
          }
        } else {
          return null
        }
      }),
  })
}
