export const ProjectResourceSchema = ProjectWithRelationsSchema.omit({
  invites: true,
  sentEmails: true,
  domains: true,
  tags: true,
  site: true,
})
  .extend({
    __meta: meta({
      extends: 'ProjectSchema',
    }),
  })
  .openapi({
    custom: {
      route_prefix: '/resources/sites',
    },
  })

