import { pinyin } from 'pinyin-pro'
import * as _ from 'radash'

const ignorePrefix = [
  '',
  '',
  '',
  '',
  '',
  '(',
  ')',
  '',
  '',
  '.',
  '',
  '',
  '',
  '',
  '',
]

export function customerName2Slug(hanzi: string) {
  ignorePrefix.forEach(p => (hanzi = hanzi.replace(p, '')))
  let py = pinyin(hanzi, { pattern: 'first', separator: '', toneType: 'none' })
  if (py.length < 6) {
    py = py + '_' + _.uid(6 - py.length).toLowerCase()
  }
  return py
}
