import { inject, injectable } from 'inversify'
import { TrpcService } from './trpc.service'
import type { INestApplication, LoggerService } from '@nestjs/common'
import { createExpressMiddleware } from '@trpc/server/adapters/express'
import { ProjectRouter } from './project.router'
import { UserRouter } from './user.router'
import { SchemaRouter } from './schema.router'
import { DataRouter } from './data.router'

@injectable()
export class TrpcRouter {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject(ProjectRouter) private projectRouter: ProjectRouter,
    @inject(UserRouter) private userRouter: UserRouter,
    @inject(DataRouter) private dataRouter: DataRouter,
    @inject(SchemaRouter) private schemaRouter: SchemaRouter,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(TrpcRouter.name)
  }

  appRouter = this.trpc.router({
    project: this.projectRouter.router,
    user: this.userRouter.router,
    schema: this.schemaRouter.schemaRouter,
    data: this.dataRouter.router,
  })

  applyMiddleware(app: INestApplication, globalPrefix: string) {
    this.logger.log(`applyMiddleware ${globalPrefix}/trpc`)
    app.use(
      `/${globalPrefix}/trpc`,
      createExpressMiddleware({
        router: this.appRouter,
      }),
    )
  }
}

export type AppRouter = TrpcRouter[`appRouter`]
