import { ContainerModule, interfaces } from 'inversify'
import { bindService } from '@flowda-projects/flowda-shared'
import { CustomZodSchemaSymbol, PrismaZodSchemaSymbol, ServiceSymbol } from '@flowda-projects/flowda-shared-types'
import { zt } from '@flowda-projects/prisma-cms_admin'
import * as schema from './lib/schema'
import { UserService } from './services/user.service'
import { CustomService } from './services/custom.service'
import { CmsAdminSchemaService } from './services/cmsAdminSchema.service'
import { DubService } from './services/dub.service'

export const cmsAdminServiceModule = new ContainerModule((bind: interfaces.Bind) => {
  bind(PrismaZodSchemaSymbol).toConstantValue(zt)
  bind(CustomZodSchemaSymbol).toConstantValue(schema)

  bind(DubService).toSelf().inSingletonScope()

  bindService(bind, ServiceSymbol, UserService)
  bindService(bind, ServiceSymbol, CustomService)
  bindService(bind, ServiceSymbol, CmsAdminSchemaService)
})
