/// <reference types="@types/multer" />

import {
  Body,
  Controller,
  Get,
  HttpCode,
  Post,
  Query,
  Req,
  Request,
  UploadedFiles,
  UseGuards,
  UseInterceptors,
} from '@nestjs/common'
import {
  addContactSchemaDto,
  CmsAdminSchemaService,
  CustomService,
  GeneratePartialSlotDataSchemaDto,
  GenerateSiteJobSchemaDto,
  GenerateSiteSchemaDto,
  GetRandomImagesQuery,
  GetTemplateDataDefSchemaDto,
  SubmitPreviewSiteSchemaDto,
  writeProjectSlugToRedisSchemaDto,
} from '@flowda-projects/cms-admin-services'
import { UserJwtAuthGuard } from '../user/userJwtAuth.guard'
import { DataService } from '@flowda-projects/flowda-shared'
import * as express from 'express'
import { AnyFilesInterceptor } from '@nestjs/platform-express'

@Controller('/apps')
export class AppController {
  constructor(
    private readonly cmsAdminSchemaService: CmsAdminSchemaService,
    private readonly custom: CustomService,
    private readonly data: DataService,
  ) {}

  @Get('/__hi')
  async hi() {
    return {
      hi: 'cms-admin-api',
    }
  }

  @Post('/submitPreviewSite')
  @UseGuards(UserJwtAuthGuard)
  async submitPreviewSite(@Request() req: any, @Body() dto: SubmitPreviewSiteSchemaDto) {
    return this.custom.submitPreviewSite(req.user, dto)
  }

  @Get('/getSchema')
  @UseGuards(UserJwtAuthGuard)
  async getSchema(@Request() req: any) {
    const schema = await this.cmsAdminSchemaService.getSchema(req.user)
    return schema
  }

  @Post('/getTemplateDataDef')
  @HttpCode(200)
  @UseGuards(UserJwtAuthGuard)
  async getTemplateDataDef(@Body() dto: GetTemplateDataDefSchemaDto) {
    return this.custom.getTemplateDataDef(dto)
  }

  @Get('/getCustomerDef')
  @HttpCode(200)
  @UseGuards(UserJwtAuthGuard)
  async getCustomerDef() {
    return this.custom.getCustomerDef()
  }

  @Get('/generateSiteJob')
  @HttpCode(200)
  @UseGuards(UserJwtAuthGuard)
  async generateJobSite(@Body() dto: GenerateSiteJobSchemaDto) {
    return this.custom.generateSiteJob(dto)
  }

  @Get('/findToDoCustomers')
  @HttpCode(200)
  @UseGuards(UserJwtAuthGuard)
  async findToDoCustomers(@Body() dto: GenerateSiteJobSchemaDto) {
    return this.custom.findToDoCustomers(dto)
  }

  @Post('/generateSite')
  @UseGuards(UserJwtAuthGuard)
  async generateSite(@Body() dto: GenerateSiteSchemaDto) {
    return this.custom.generateSite(dto)
  }

  @Post('/generatePartialSlotData')
  @UseGuards(UserJwtAuthGuard)
  async generatePartialSlotData(@Body() dto: GeneratePartialSlotDataSchemaDto) {
    return this.custom.generatePartialSlotData(dto)
  }

  @Get('/getRandomImages')
  @UseGuards(UserJwtAuthGuard)
  async getRandomImages(@Query() query: GetRandomImagesQuery) {
    return this.custom.getRandomImages(query)
  }

  @Post('/writeProjectSlugToRedis')
  @UseGuards(UserJwtAuthGuard)
  async writeProjectSlugToRedis(@Body() dto: writeProjectSlugToRedisSchemaDto) {
    return this.custom.writeProjectSlugToRedis(dto)
  }

  @Post('/addContact')
  async addContact(@Body() dto: addContactSchemaDto) {
    return this.custom.addContact(dto)
  }

  @Get('/sites')
  async getSites(@Req() req: express.Request) {
    return this.data.get({}, 'sites', req.query)
  }

  @Post('upload')
  @UseInterceptors(AnyFilesInterceptor())
  @UseGuards(UserJwtAuthGuard)
  uploadFile(@UploadedFiles() files: Express.Multer.File[]) {
    return this.custom.parseKanzhun(files)
  }

  @Post('/updateSlotDataContact')
  @UseGuards(UserJwtAuthGuard)
  async updateSlotDataContact(@Req() req: express.Request) {
    return this.custom.updateSlotDataContact()
  }

  @Post('/batchCreateProjectFromSite')
  @UseGuards(UserJwtAuthGuard)
  async batchCreateProjectFromSite(@Req() req: express.Request) {
    return this.custom.batchCreateProjectFromSite()
  }

  @Post('/batchWriteProjectSlugToRedis')
  @UseGuards(UserJwtAuthGuard)
  async batchWriteProjectSlugToRedis(@Req() req: express.Request) {
    return this.custom.batchWriteProjectSlugToRedis()
  }
}
